import React, { Component } from 'react'
import { Platform, Text, View, ActivityIndicator, ScrollView } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../Services/NavigationService'

import FirebaseActions from '../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../Stores/Firebase/Selectors'

import { Button, Comment_Module } from '../Components'
import { ApplicationStyles, Helpers, Metrics } from '../Theme'

export const Operations = {
  ADD: 'Add pig',
  MOVE: 'Move pig',
  LOST: 'Lost pig',
  SOLD: 'Sold pig',
}

class OperationsPanel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      View:1,
    }
  }

  getPenAndSection() {
    var Section, Pen = undefined
    if (this.props.pig && this.props.pigTag && this.props.PigsList) {
      if (this.props.PigsList[this.props.pigTag] && this.props.PigsList[this.props.pigTag].pen && this.props.PigsList[this.props.pigTag].pen != 0) {
        Pen = this.props.PigsList[this.props.pigTag].pen

        const localDataStore = this.props.firebase.localDataStore
        if (localDataStore && localDataStore.pens && localDataStore.pens[Pen]) {
          var PenData = localDataStore.pens[Pen];
          Section = PenData.section;
        }
      }
    }
    return {Section, Pen}
  }

  getData() {
    
  }

  componentDidMount() {
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      
    }
  }

  render() {
    const localDataStore = this.props.firebase.localDataStore //Farmid
    const CurrentData = this.props.firebase.currentData

    if (this.props.pig) {
      return(
        <View style={[Helpers.buttonContainer]}>
          <Button Text="Add Pig"
            onPress={() => {
              if (this.props.pigTag !== "" ) {
                if (Object.keys(this.props.PigsList).filter(e => e === this.props.pigTag).length > 0){
                  this.props.setFirebaseError("Can not add Tag already exist")
                } else {
                  this.props.setFirebaseError("")
                  this.props.newPig(Object.keys(localDataStore.userData.farms)[0], this.props.pigTag)
                }
              }
              else this.props.setFirebaseError("A Tag is needed")
            } }/>
          <Button Text="Move Pig"
            onPress={() => {
              var {Section, Pen} = this.getPenAndSection()
              this.props.setCurrentPig(this.props.pigTag, Section, Pen)
              NavigationService.navigate("DataEditor")
            } }/>
        </View>
      )
    } else if (this.props.section) {
      return(
        <View style={[Helpers.buttonContainer]}>
          <Button Text="Weight"
              onPress={() => NavigationService.navigate("WeighingOperations") 
            }/>
          <View style={[{marginTop:40}]}>
            <Button Text="Add Pen"
              onPress={() => {
                this.props.editPens(Action.CREATE)
                NavigationService.navigate("DataEditor")
              } }/>
            <Button Text="Remove Pen"
              onPress={() => {
                this.props.editPens(Action.DELETE)
                NavigationService.navigate("DataEditor")
              } }/>
          </View>
        </View>
      )
    } else {
      var Action = 1;
      switch(this.state.View) {
        case Operations.MOVE:
        case Operations.LOST:
        case Operations.SOLD:
          Action = -1
        case Operations.ADD:
          return(
            <Comment_Module Title={"Operations comment:"} onDone={(res) => {
              if (res.res) {
                var action = this.state.View
                action = action.charAt(0).toUpperCase() + action.slice(1)

                this.props.newComment(Object.keys(localDataStore.userData.farms)[0], CurrentData.penId, action, res.data)
                this.props.newPigCount(Object.keys(localDataStore.userData.farms)[0], CurrentData.penId, Action)
              }

              this.setState({
                View: 1
              })
            }}/>
          )
          //this.props.newPigCount(Object.keys(localDataStore.userData.farms)[0], CurrentData.penId, Action * "count_input", comment)

        default:
          return(
            <ScrollView style={{paddingRight: 5}}>
              <View style={[Helpers.buttonContainer]}>
                <Button Text="Weight"
                    onPress={() => NavigationService.navigate("WeighingOperations") 
                  }/>
                <View style={[{marginTop:10}]}>
                  <Button Text="Add Pig"
                    onPress={() => {
                      this.setState({
                        View: Operations.ADD,
                      })
                    } }/>
                  <Button Text="Move Pig"
                    onPress={() => {
                      this.setState({
                        View: Operations.MOVE,
                      })
                    } }/>
                  <Button Text="Lost Pig"
                    onPress={() => {
                      this.setState({
                        View: Operations.LOST,
                      })
                      //TODO: Add lost pig data save
                      //dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: Object.keys(localDataStore.userData.farms)[0], PenId: props.penid, Action:-1}))
                    } }/>
                  <Button Text="Sold Pig"
                    onPress={() => {
                      this.setState({
                        View: Operations.SOLD,
                      })
                      //TODO: Add sold pig data save
                      //dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: Object.keys(localDataStore.userData.farms)[0], PenId: props.penid, Action:-1}))
                    } }/>
                </View>
              </View>
            </ScrollView>
          )
      }
    }
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    setCurrentData: penId => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, penId: penId})),
    setCurrentDataElement: element => dispatch(FirebaseActions.setCurrentData(element)),

    editPens: (action) => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, action:action})),

    setCurrentPig: (pigTag, Section, Pen) => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PIGS, action:Actions.CREATE, pigtag:pigTag, data:{section:Section}, dataId:Pen})),
    newPig: (farmId, pigTag) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: farmId, TagId: pigTag})),
    newPigCount: (farmId, penId, countChange) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.PIGS, {FarmId: farmId, PenId: penId, Action:countChange})),

    newComment: (farmId, penId, action, data) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.COMMENTS, {FarmId: farmId, PenId: penId, Action: action, Data: data})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(OperationsPanel);