import React, { useState } from 'react';
import { View, Text, TextInput, Image, StyleSheet, RecyclerViewBackedScrollViewComponent} from 'react-native';
import Button from './Button'
import { Icon, Input } from 'react-native-elements'

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors } from '../Theme'

const Comment_Module = props => {
  const Title = props.Title;

  const onDone = props.onDone;
  var Button1 = "Done";
  var Button2 = "Cancel";
  const [InputText, setInputText] = useState("");

  return(
    <View style={[Helpers.buttonContainer, props.style] }>
      <View style={styles.textAreaContainer} >
        {Title?(<Text style={[Fonts.normal, {height: 24, color:Colors.input, fontWeight:700}]}>{Title}</Text>):(<></>)}
        <TextInput
          style={styles.textArea}
          underlineColorAndroid="transparent"
          placeholder="Type something"
          placeholderTextColor="grey"
          numberOfLines={10}
          multiline={true}
          onChangeText={(text) => setInputText(text)}
          value={InputText}
        />
      </View>
      <Button key={"_Done"} Text={Button1}
        onPress={() => {
          if (onDone) {
            onDone({res: true, data: InputText})
          }
        }}/>
      <Button key={"_Cancel"} Text={Button2}
        onPress={() => {
          if (onDone) {
            onDone({res: false, data: InputText})
          }
        }}/>
    </View>
  )
};

const styles = StyleSheet.create({
  textAreaContainer: {
    borderColor: Colors.gray,
    borderWidth: 1,
    padding: 5
  },
  textArea: {
    height: 150,
    justifyContent: "flex-start",
    borderColor: "black",
    borderWidth: 0.5,
    padding: 5,
  }
})

export default Comment_Module;