function format_num(num) {
    if (num < 10){
        num = "0"+num
    }
    return num;
}
export function format_Time(time) {
    var s = new Date(parseInt(time));
    var datetime = format_num(s.getDate()) + "/" + format_num(s.getMonth() +1) + "/" + s.getFullYear() + " " 
                    + format_num(s.getHours()) + ":" + format_num(s.getMinutes()) + ":" + format_num(s.getSeconds());
    return datetime
}

var reA = /[^a-zA-Z]/g;
var reN = /[^0-9]/g;
function sortAlphaNum(a, b) {
  var aA = a.replace(reA, "");
  var bA = b.replace(reA, "");
  if (aA === bA) {
    var aN = parseInt(a.replace(reN, ""), 10);
    var bN = parseInt(b.replace(reN, ""), 10);
    return aN === bN ? 0 : aN > bN ? 1 : -1;
  } else {
    return aA > bA ? 1 : -1;
  }
}

export function sortObject(o) {
    var sorted = {},
    key, a = [];

    for (key in o) {
        if (o.hasOwnProperty(key) && key !== "__jsogObjectId" && key !== "update_time") {
            a.push(key);
        }
    }
    a.sort(sortAlphaNum);

    for (key = 0; key < a.length; key++) {
        sorted[a[key]] = o[a[key]];
    }
    return sorted;
}

export function get_DataSpot_FromData(localDataStore, PenId) {
    if (localDataStore.weighings && localDataStore.weighings[PenId]) {
        var Weighing = null;
        for (const _weighing in localDataStore.weighings[PenId]) {
          var weighing = localDataStore.weighings[PenId][_weighing]
          if (weighing && (!Weighing || Weighing.time < weighing.time)) {
            Weighing = weighing
          }
        }

        if (Weighing) {
            var weight = Weighing.weight
            weight = weight / Weighing.count
            if (weight && !isNaN(weight)) {
                //If batch
                var Batchs = null
                for (const _batch in localDataStore.batchs) {
                    var batch = localDataStore.batchs[_batch]
                    if (batch.hasOwnProperty("pens") && batch.pens[PenId] != null) {
                        Batchs = localDataStore.batchs[_batch]
                        //break; //diffrenc from web ???
                    }
                }
                if (Batchs) {
                    var Batch = Batchs
                    if (Batch && Batch.time && Batch.datas 
                        && localDataStore.graphs && localDataStore.graphs[Batch.gcurve]) {
                        var Curve = localDataStore.graphs[Batch.gcurve]
                        var Keys = Object.keys(Curve.data);
                        var WeighingTime = Weighing.time

                        if (WeighingTime > Batch.time && (!Batch.state || Batch.state !== "Closed")) {
                            var StartTime = Batch.time
                            var EndTime = Batch.time
                            var index = 0;
                            for (index = 0; index < Curve.data.length-1; index++) {
                                var currentTime = new Date(StartTime)
                                switch(Curve.periode) {
                                case "Days":
                                    currentTime = new Date(currentTime.setDate(currentTime.getDate() +1))
                                    break;
                                case "Month":
                                    currentTime = new Date(currentTime.setMonth(currentTime.getMonth() + 1))
                                    break;
                                case "Weeks":
                                default:
                                    currentTime = new Date(currentTime.setDate(currentTime.getDate() + 7))
                                    break;
                                }
                                EndTime = currentTime.getTime()
                                if (EndTime >= WeighingTime){
                                break;
                                }else {
                                StartTime = EndTime
                                }
                            }
                            //var EndDate = EndDate = currentTime.getTime();
                            var DataSpot = get_DataSpot([StartTime, EndTime], [Curve.data[Keys[index]], Curve.data[index +1]], new Date().getTime(), weight, WeighingTime)
                            DataSpot.Batch = Batch
                            return DataSpot
                        }
                    }
                }
            }
        }
    }
    return null
}

export function get_DataSpot(MinMax_Time, MinMax_Data, CalcTime, Data, DataTime) {
    var ExValue = null
    var VirData = null
    if (CalcTime) {
        // Math.Atan of MaxY - MinY / MaxX - MinX
        var minX = 0
        var maxX = MinMax_Time[1] - MinMax_Time[0]
        var minY = MinMax_Data[0]
        var maxY = MinMax_Data[1]

        var slope = Math.atan( (maxY - minY) / (maxX - minX) )
        
        // ExValue = ((FindX - MinX) * Slope) + MinY
        var getx = (CalcTime - MinMax_Time[0])
        ExValue = (getx * slope) + minY

        if (Data && DataTime) {
            var VirGain = (slope * (CalcTime - DataTime))
            VirData = VirGain + Data
        }
    }
    return {ExValue, VirData}
}