import React, { useEffect, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, get_DataSpot, get_DataSpot_FromData } from '../../../Stores/Firebase/Selectors'

import { LineChart } from 'react-native-chart-kit'
import { Button, TitleElement, CustomListItem, ContentProp } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'
import CurveElement, { WeightsToGraphData, AccumulateTime } from './CurveElement'

import { FlatGrid } from 'react-native-super-grid';

class DataDisplay extends Component {
  constructor(props) {
    super(props);

    /*const {DataSets, StartDate, EndDate } = this.setData(props)
    this.state = {
      DataSets: DataSets,
      StartDate: StartDate,
      EndDate: EndDate,
    }*/
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const CurrentData = Props.firebase.currentData;

    var TempGraph = null //Template Graph
    var Legends = []     //Name lables
    var DataSets = []    //Data Points

    var D = new Date()
    var StartDate = D.getTime()
    var EndDate = new Date(D.setDate(D.getDate()+(7*8))) //Default timespand is 2 months or 8 weeks (will be changed acording to batch)

    if (localDataStore) {
      if (localDataStore.batchs && CurrentData.batchId && 
          localDataStore.graphs && CurrentData.curveId) {
        var TempGraph = localDataStore.graphs[CurrentData.curveId];
        if (TempGraph) {
          TempGraph.name = CurrentData.curveId
        } else {
          return {TempGraph, Legends, DataSets, StartDate, EndDate }
        }
        var Batch = localDataStore.batchs[CurrentData.batchId];

        //Find Startdate
        StartDate = Batch.time
        //Find Batch Pens (Legends)
        Legends = Legends.concat(Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')))

        var DataX = []
        //#region Find Template X values and EndDate
        if (!TempGraph.periode){
          TempGraph.periode = "Weeks"
        }
        var currentTime = new Date(StartDate)
        for (var i = 0; i < TempGraph.data.length; i++) {
          DataX.push(currentTime.getTime())
          if (i < TempGraph.data.length -1) {
            currentTime = new Date(AccumulateTime(currentTime, TempGraph.periode))
          }
        }
        EndDate = currentTime.getTime();
        DataX.push(EndDate)
        //#endregion

        //Set TempGraph data
        DataSets.push({
          data: TempGraph.data,
          dataX: DataX,
          color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`, // optional
        })
      }

      //Find DataSets from Legends
      if (localDataStore.weighings) {
        var TempData = DataSets
        var TempEndDate = EndDate
        var NextEnd = new Date(AccumulateTime(new Date(TempEndDate),TempGraph.periode)).getTime()
        var {DataSets, EndDate} = WeightsToGraphData(Legends, TempGraph.data.length, localDataStore.weighings, StartDate, NextEnd)
        DataSets = TempData.concat(DataSets)
        if (TempEndDate > EndDate) {
          EndDate = TempEndDate
        }

        Legends = Legends.filter(e => (e !== '__jsogObjectId' && (e === TempGraph.name || DataSets.some(i => i.Pen === e))));
        Legends.unshift(TempGraph.name)
      }
    }
    return {DataSets, StartDate, EndDate }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    const CurrentData = this.props.firebase.currentData;

    if (CurrentData && CurrentData.batchId && localDataStore.batchs[CurrentData.batchId]){
      const batchData = localDataStore.batchs[CurrentData.batchId]
      
      Object.keys(batchData.pens).forEach(Pen => {
        if (Pen === "__jsogObjectId") return null
        this.props.getPen(Object.keys(localDataStore.userData.farms)[0], Pen)
        this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], Pen)
      })

      var TempGraph = ""
      if (CurrentData.curveId) {
        TempGraph = CurrentData.curveId;
      }
      this.props.getCurve(Object.keys(localDataStore.userData.farms)[0], TempGraph)
    }
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("AuthStack")
    }
    if (!props.firebase.currentData || !props.firebase.currentData.batchId){
      NavigationService.navigate("UserBatchs")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.localDataStore) {
        if (JSON.stringify(prevProps.firebase.localDataStore.weighings) !== JSON.stringify(this.props.firebase.localDataStore.weighings) ||
            JSON.stringify(prevProps.firebase.localDataStore.pens) !== JSON.stringify(this.props.firebase.localDataStore.pens) ||
            JSON.stringify(prevProps.firebase.localDataStore.graphs) !== JSON.stringify(this.props.firebase.localDataStore.graphs)) {
          //const {TempGraph, Legends, DataSets, StartDate, EndDate } = this.setData(this.props)

          /*if (JSON.stringify(this.state.DataSets)!==JSON.stringify(DataSets) ||

              JSON.stringify(this.state.StartDate)!==JSON.stringify(StartDate) ||
              JSON.stringify(this.state.EndDate)!==JSON.stringify(EndDate)
          ) {
            this.setState({
              TempGraph: TempGraph,
              Legends: Legends,
              DataSets: DataSets,
        
              StartDate: StartDate,
              EndDate: EndDate,
            })
          }*/
        }
      }
    }
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid
    const CurrentData = this.props.firebase.currentData;

    var Batch = localDataStore.batchs[this.props.firebase.currentData.batchId];
    var TempGraph = localDataStore.graphs[CurrentData.curveId]

    var currentTime = new Date(Batch.time)
    for (var i = 0; i < TempGraph.data.length; i++) {
      if (i < TempGraph.data.length -1) {
        currentTime = new Date(AccumulateTime(currentTime, TempGraph.periode))
      }
    }
    var EndDate = currentTime.getTime();

    var isVirtual = false;
    var Animals = 0
    var ExpectedWeight = 0;
    var AverageWeight = 0;
    var AverageGrowth = 0;
    Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')).forEach(element => {
      if (localDataStore && localDataStore.pens && localDataStore.pens[element]) {
        var Pen = localDataStore.pens[element]
        if (Pen.datas && Pen.datas.count) {
          Animals += Pen.datas.count;
        }
        if (localDataStore.weighings && localDataStore.weighings[element]) {
          var dataSpot = get_DataSpot_FromData(localDataStore, element)
          if (ExpectedWeight === 0 ) {
            ExpectedWeight = parseFloat(dataSpot.ExValue).toFixed(2)
          }

          var PenWeight = 0
          var lastDate = 0
          var AvagPenGrowth = 0
          Object.keys(localDataStore.weighings[element]).forEach((weight) => {
            var Weight = localDataStore.weighings[element][weight]
            if (Weight.time >= Batch.time && Weight.time <= EndDate) {
              if (PenWeight !== 0) {
                var Growth_Days = (Weight.time - lastDate) / (1000 * 3600 * 24);
                var PenGrowth = (Weight.weight - PenWeight) / Growth_Days
                if (AvagPenGrowth === 0) AvagPenGrowth = PenGrowth
                else AvagPenGrowth = ((AvagPenGrowth + PenGrowth) / 2) 
              }
              PenWeight = Weight.weight / Weight.count;
              lastDate = Weight.time
            }
          })

          var now = new Date().getTime()
          if (now <= EndDate && new Date(now).getDate() > new Date(lastDate).getDate()){
            if (lastDate !== 0){
              var VirData = dataSpot.VirData
              var Growth_Days = (now - lastDate) / (1000 * 3600 * 24);
              var PenGrowth = (VirData - PenWeight) / Growth_Days
              if (AvagPenGrowth === 0) AvagPenGrowth = PenGrowth
              else AvagPenGrowth = ((AvagPenGrowth + PenGrowth) / 2)
            }
            PenWeight = dataSpot.VirData
            isVirtual = true
          }

          if (AverageWeight === 0) AverageWeight = PenWeight;
          else AverageWeight = ((AverageWeight + PenWeight) / 2)
          if (AverageGrowth === 0) AverageGrowth = AvagPenGrowth;
          else AverageGrowth = ((AverageGrowth + AvagPenGrowth) / 2)
        }
      }
    });
    AverageWeight = parseFloat(AverageWeight).toFixed(2)
    AverageGrowth = parseFloat(AverageGrowth).toFixed(2)

    var Dif_Time = new Date().getTime() - Batch.time
    var Days = Dif_Time / (1000 * 3600 * 24);
    Days = Days.toFixed(0)
    
    var StartDate = new Date(Batch.time).getDate() + "/" + (new Date(Batch.time).getMonth() +1) + "/" + new Date(Batch.time).getFullYear()
    var EndDate = new Date(EndDate).getDate() + "/" + (new Date(EndDate).getMonth() +1) + "/" + new Date(EndDate).getFullYear()

    var Virtual = String(isVirtual) 
    Virtual = Virtual.charAt(0).toUpperCase() + Virtual.slice(1)
    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          <TitleElement Text={this.props.firebase.currentData.batchId?this.props.firebase.currentData.batchId:"-NON-"}/>
          <View>
            <View style={[Helpers.fullWidth, {justifyContent:'center'}]}>
              <FlatGrid
                itemDimension={120}
                spacing={10}

                data={[
                  {title: 'Start', data: StartDate},
                  {title: 'End Forecast', data: EndDate},
                  {title: 'Target Weight', data: TempGraph.data[TempGraph.data.length -1]},
                  //{title: 'Virtual cal', data: Virtual}
                ]}
                renderItem={({ item }) => (
                  <View style={[Helpers.borderContainer, {textAlign:'center'}]}>
                    <Text>{item.title}</Text>
                    <Text>{item.data}</Text>
                  </View>
                )}
                />
            </View>
            <View style={[Helpers.fullWidth, {justifyContent:'center'}]}>
              <FlatGrid
                itemDimension={120}
                spacing={10}

                data={[
                  {title: 'Animals', data: Animals},
                  {title: 'Day', data: Days},
                  {title: 'Avarage weight', data: AverageWeight},
                  {title: 'Average growth', data: AverageGrowth}
                ]}
                renderItem={({ item }) => (
                  <View style={[Helpers.borderContainer, {textAlign:'center'}]}>
                    <Text>{item.title}</Text>
                    <Text>{item.data}</Text>
                  </View>
                )}
                />
            </View>
          </View>
          <CurveElement />
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    
    getPen: (farmId, penId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {FarmId: farmId, PenId: penId})),
    getWeights: (farmid, penId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {FarmId: farmid, PenId: penId, limitTo: 99})),

    getBatch: (farmId, batchId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {FarmId: farmId})),
    getCurve: (farmid, curveid) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {FarmId: farmid, GraphId: curveid})),
    setCurve: (farmid, curve) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.GRAPH, {FarmId: farmid, Graph: curve}))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataDisplay);
//export default DataDisplay;