function getRandomNumber(low, high) {
  var r = Math.floor(Math.random() * (high - low + 1)) + low;
  return r;
}

function getRandomColor() {
  var red_range = [10, 250];
  var green_range = [10, 250];
  var blue_range = [10, 250];
  var a_range = [0.2, 1];

  var red = getRandomNumber(red_range[0], red_range[1]);
  var green = getRandomNumber(green_range[0], green_range[1]);
  var blue = getRandomNumber(blue_range[0], blue_range[1]);

  var alpha = getRandomNumber(a_range[0] * 100, a_range[1] * 100) / 100;

  return {
    r: red,
    g: green,
    b: blue,
    a: alpha,
    rgbaValue: 'rgba(' + red + ',' + green + ',' + blue + ',' + alpha + ')'
  }
}

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

function AccumulateTime(Time, PeriodeLength){
  var NewTime = Time
  switch(PeriodeLength) {
    case "Days":
      NewTime.setDate(NewTime.getDate() + 1)
      break;
    case "Month":
      NewTime.setMonth(NewTime.getMonth() + 1)
      break;
    case "Weeks":
    default:
      NewTime.setDate(NewTime.getDate() + 7)
      break;
  }
  return NewTime;
}

function WeightsToGraphData(Legends, TempGraph_DataLength, PenWeighings, StartDate, _EndDate = undefined) {
  var DataSets = []
  var EndDate = 0;

  Legends.forEach(Pen => {
    var DataSet = []
    var DataSetX = []

    if (PenWeighings[Pen]) {
      var Weighings = PenWeighings[Pen];
      if (Object.keys(Weighings).length >= 1) {
        Object.keys(Weighings).sort(function(a, b) {
          return Weighings[a].time - Weighings[b].time;
        }).forEach(key => {
          var data = Weighings[key]
          if (data.weight && data.time >= StartDate && !(_EndDate && data.time > _EndDate)) {
            var weight = data.weight
            if (data.count){
              weight = weight / data.count
            }
            DataSet.push(weight)
            var Time = new Date(data.time)
            Time = new Date(Time.getFullYear(),Time.getMonth(),Time.getDate())
            DataSetX.push(Time)
            if (!_EndDate && data.time > EndDate) {
              EndDate = data.time
            }
          }
        });
      }
    }
    while(DataSet.length < TempGraph_DataLength) {
      DataSet.push(null)
      DataSetX.push(null)
    }
  
    var color = getRandomColor();
    //if (DataSet.some(function(i) { return i !== null; })) {
      DataSets.push({Pen: Pen, data: DataSet, dataX: DataSetX, color:(opacity = 1) => color.rgbaValue})
    //}
  });
  return {DataSets, EndDate};
}

class CurveElement extends Component {
  constructor(props) {
    super(props);

    if (!this.props.Display) {
      const {TempGraph, Legends, DataSets, StartDate, EndDate } = this.setData(props)
      this.state = {
        dimensions: {window, screen},

        TempGraph: TempGraph,
        Legends: Legends,
        DataSets: DataSets,
        StartDate: StartDate,
        EndDate: EndDate,

        Labels: [],
        ActiveIndex: 0,
        ActiveLength: "Batch",
        ActiveDataSets: DataSets,
        ActiveStartDate: StartDate,
        ActiveEndDate: EndDate,

        ViewPoint: null,
      }
    } else {
      this.state = {
        dimensions: {window, screen},
      }
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const CurrentData = Props.firebase.currentData;

    var TempGraph = null //Template Graph
    var Legends = []     //Name lables
    var DataSets = []    //Data Points

    var D = new Date()
    var StartDate = D.getTime()
    var EndDate = new Date(D.setDate(D.getDate()+(7*8))) //Default timespand is 2 months or 8 weeks (will be changed acording to batch)

    if (localDataStore) {
      if (localDataStore.batchs && CurrentData.batchId && 
          localDataStore.graphs && CurrentData.curveId) {
        var TempGraph = localDataStore.graphs[CurrentData.curveId];
        if (TempGraph) {
          TempGraph.name = CurrentData.curveId
        } else {
          return {TempGraph, Legends, DataSets, StartDate, EndDate }
        }
        var Batch = localDataStore.batchs[CurrentData.batchId];

        //Find Startdate
        StartDate = Batch.time
        //Find Batch Pens (Legends)
        Legends = Legends.concat(Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')))

        var DataX = []
        //#region Find Template X values and EndDate
        if (!TempGraph.periode){
          TempGraph.periode = "Weeks"
        }
        var currentTime = new Date(StartDate)
        for (var i = 0; i < TempGraph.data.length; i++) {
          DataX.push(currentTime.getTime())
          if (i < TempGraph.data.length -1) {
            currentTime = new Date(AccumulateTime(currentTime, TempGraph.periode))
          }
        }
        EndDate = currentTime.getTime();
        DataX.push(EndDate)
        //#endregion

        //Set TempGraph data
        DataSets.push({
          data: TempGraph.data,
          dataX: DataX,
          color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`, // optional
        })
      }

      //Find DataSets from Legends
      if (localDataStore.weighings) {
        var TempData = DataSets
        var TempEndDate = EndDate
        var NextEnd = new Date(AccumulateTime(new Date(TempEndDate),TempGraph.periode)).getTime()
        var {DataSets, EndDate} = WeightsToGraphData(Legends, TempGraph.data.length, localDataStore.weighings, StartDate, NextEnd)
        DataSets = TempData.concat(DataSets)
        if (TempEndDate > EndDate) {
          EndDate = TempEndDate
        }

        Legends = Legends.filter(e => (e !== '__jsogObjectId' && (e === TempGraph.name || DataSets.some(i => i.Pen === e))));
        Legends.unshift(TempGraph.name)
      }
    }
    return {TempGraph, Legends, DataSets, StartDate, EndDate }
  }

  setGraphPeriode(ActiveLength = this.state.ActiveLength) {
    var Labels = []

    var StartDate = this.state.StartDate
    var EndDate = this.state.EndDate
    var ActiveIndex = this.state.ActiveIndex

    var TemplatePeriode = this.state.TempGraph.periode
    var CurrentDate = new Date(StartDate)
    var TempActiveDataSet = this.state.ActiveDataSets;

    var Today = new Date().getTime()
    switch(this.state.ActiveLength) {
      case "Days":
        for (var i = 0; (i < ActiveIndex || ActiveIndex === -1); i++){
          var getNewDate = new Date(AccumulateTime(CurrentDate, this.state.ActiveLength))
          if (ActiveIndex === -1 && getNewDate > Today) {
            ActiveIndex = i-1;
            break;
          }
          CurrentDate = getNewDate;
        }
        StartDate = CurrentDate.getTime();
        Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
        
        CurrentDate = new Date(AccumulateTime(CurrentDate, this.state.ActiveLength))
        Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))

        EndDate = CurrentDate.getTime();
        CurrentDate = new Date(AccumulateTime(CurrentDate, this.state.ActiveLength))
        break;
      case "Weeks":
        for (var i = 0; (i < ActiveIndex || ActiveIndex === -1); i++){
          var getNewDate = new Date(AccumulateTime(CurrentDate, this.state.ActiveLength))
          if (ActiveIndex === -1 && getNewDate > Today) {
            ActiveIndex = i;
            break;
          }
          CurrentDate = getNewDate;
        }
        StartDate = CurrentDate.getTime();

        for (var i = 0; i <= 7; i++) {
          Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
          EndDate = CurrentDate.getTime();
          CurrentDate = new Date(AccumulateTime(CurrentDate, "Days"))
        }
        break;
      case "Batch":
      default:
        while (CurrentDate.getTime() <= EndDate) {
          switch (TemplatePeriode){
            case "Days":
              Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
              break;
            case "Month":
              Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
              break;
            case "Weeks":
            default:
              Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
              break;
          }
          CurrentDate = new Date(AccumulateTime(CurrentDate, TemplatePeriode))
        }
        break;
    }

    var ActiveDataSets = []
    if (this.ActiveLength === "Batch") ActiveDataSets = this.state.DataSets;
    else {
      var End = AccumulateTime(new Date(EndDate), this.state.ActiveLength).getTime()
      this.state.DataSets.forEach(element => {
        var NewData = {...element, data:[], dataX: []}

        var first = null
        var last = null
        if (new Date(element.dataX[0]).getTime() <= EndDate) {
          for(var i = 0; i < element.data.length; i++) {
            if (new Date(element.dataX[i]).getTime() >= StartDate) {
              NewData.data.push(element.data[i])
              NewData.dataX.push(element.dataX[i])

              var time = AccumulateTime(new Date(element.dataX[i]), this.state.ActiveLength)
              if (time.getTime() > End){
                if (element.dataX.length -1 >= i) last = i
                break;
              }
            } else if (element.dataX[i] !== null) {
              first = i
            }
          }
          if (first !== null && NewData.dataX[0] > StartDate && element.data.length >= first+1) {
            var {ExValue, VirData} = get_DataSpot([element.dataX[first], element.dataX[first+1]], [element.data[first], element.data[first +1]], StartDate, element.data[first], element.dataX[first])
            NewData.data.unshift(ExValue)
            NewData.dataX.unshift(StartDate)
          }
          if (last != null) {
            //NewData.data.push(ExValue)
            //NewData.dataX.push(StartDate)
          }
        }

        ActiveDataSets.push(NewData)
      });
    }

    var lastDataPoint = this.state.DataSets[0].dataX[this.state.DataSets[0].dataX.length-1]
    if (this.state.ActiveIndex > 0 && ActiveDataSets[0].data.length === 0){
      var i = this.state.ActiveIndex
      i = i-1;
      this.setState({
        ActiveIndex: i
      })
    } else {
      this.setState({
        ActiveLength: ActiveLength,
        
        Labels: Labels,
        ActiveDataSets: ActiveDataSets,
        ActiveStartDate: StartDate,
        ActiveEndDate: EndDate,
        ActiveIndex: ActiveIndex,
      }, () => {
        //this.forceUpdate()
      })
    }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    const CurrentData = this.props.firebase.currentData;

    if (CurrentData && CurrentData.batchId && localDataStore.batchs[CurrentData.batchId]){
      const batchData = localDataStore.batchs[CurrentData.batchId]
      
      Object.keys(batchData.pens).forEach(Pen => {
        if (Pen === "__jsogObjectId") return null
        this.props.getPen(Object.keys(localDataStore.userData.farms)[0], Pen)
        this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], Pen)
      })

      var TempGraph = ""
      if (CurrentData.curveId) {
        TempGraph = CurrentData.curveId;
      }
      this.props.getCurve(Object.keys(localDataStore.userData.farms)[0], TempGraph)
    }
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidUpdate(prevProps, prevState) {
    if (!this.props.Display && this.props !== prevProps) {
      if (this.props.firebase.localDataStore) {
        if (JSON.stringify(prevProps.firebase.localDataStore.weighings) !== JSON.stringify(this.props.firebase.localDataStore.weighings) ||
            JSON.stringify(prevProps.firebase.localDataStore.pens) !== JSON.stringify(this.props.firebase.localDataStore.pens) ||
            JSON.stringify(prevProps.firebase.localDataStore.graphs) !== JSON.stringify(this.props.firebase.localDataStore.graphs)) {
          const {TempGraph, Legends, DataSets, StartDate, EndDate } = this.setData(this.props)

          if (JSON.stringify(this.state.TempGraph)!==JSON.stringify(TempGraph) ||
              JSON.stringify(this.state.Legends)!==JSON.stringify(Legends) ||
              JSON.stringify(this.state.DataSets)!==JSON.stringify(DataSets) ||

              JSON.stringify(this.state.StartDate)!==JSON.stringify(StartDate) ||
              JSON.stringify(this.state.EndDate)!==JSON.stringify(EndDate)
          ) {
            this.setState({
              TempGraph: TempGraph,
              Legends: Legends,
              DataSets: DataSets,
        
              StartDate: StartDate,
              EndDate: EndDate,
            })
          }
        }
      }
    }
    
    if (!this.props.Display && this.state !== prevState) {
      if (this.state.ActiveLength != prevState.ActiveLength || this.state.ActiveIndex != prevState.ActiveIndex) {
        this.setGraphPeriode(this.state.ActiveLength)
      }
    }
  }

  componentDidMount() {
    this.getData();
    if (!this.props.height) Dimensions.addEventListener("change", this.onChange);
    if (!this.props.Display) this.setGraphPeriode(this.state.ActiveLength)
  }

  componentWillUnmount(){
    if (!this.props.height) Dimensions.removeEventListener("change", this.onChange);
  }

  render() {
    const localDataStore = this.props.firebase.localDataStore //Farmid
    var BatchIsOpen = (!localDataStore.batchs[this.props.firebase.currentData.batchId].state || localDataStore.batchs[this.props.firebase.currentData.batchId].state !== "Closed")?true:false
    var wide = this.state.dimensions.window.width>1000?true:false
    if (this.props.Display) wide = false

    var height = this.state.dimensions.window.height //Full Screen
    if (this.props.Height) {
      height = this.props.Height;
      height = height - 100 // ?? beautify'er
    } else {
      if (Platform.OS !== 'ios' && Platform.OS !== 'android') height = height - 64 //Navigation title
      height = height - 49 //TitleElement
      height = height - 145 //Data Props
      height = height - 71 //View Selector
      if (BatchIsOpen) height = height - 74 //Editor

      height = height - 100 // ?? beautify'er
    }
    height = height - parseInt(10 + 15) //Margins 
    
    var width = this.state.dimensions.window.width * 2; //Full Screen
    if (this.props.Width) {
      width = this.props.Width;
    } else {
      width = width - (Metrics.normal * 4) // Margins
      width = width / (wide?2.05:1) //If not WideScreen - Split diagram in 2
    }
    
    var DataSets = this.props.Display?[{color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`,data:[],dataX:[]}]:this.state.ActiveDataSets
    var Legends = this.props.Display?[]:this.state.Legends.slice(0, DataSets.length)

    var StartDate = this.props.Display?1:this.state.ActiveStartDate
    var EndDate = this.props.Display?this.props.DataPoints.length:this.state.ActiveEndDate
    var Labels = [];
    if (this.props.Display){
      console.log(this.props.DataPoints)
      this.props.DataPoints.forEach((point,index)=>{
        DataSets[0].data.push(point.value)
        DataSets[0].dataX.push(index +1)
        Labels.push(index +1)
      })
    }else Labels = this.state.Labels
    return(
      <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill, {marginHorizontal: 0, paddingHorizontal:-20}]}>
          <ScrollView horizontal={true} showsHorizontalScrollIndicator={wide||this.props.Display?false:true} style={[Helpers.fullSize]}>
            {this.state.TempGraph||this.props.Display && (Labels && Labels.length > 0) && (DataSets && DataSets.length > 0)?(
            <LineChart
              //bezier
              style={[{marginLeft: -15, marginRight:Metrics.normal, minHeight: height, minWidth: width}]}
              width={width}
              height={height}

              data={{
                labels: Labels,
                xMinMax: [StartDate, EndDate, Labels.length -1],
                legend: Legends,
                datasets: DataSets,
              }}

              onDataPointClick={(data) => {
                this.setState({
                  ViewPoint: data
                })
              }}

              withShadow={false}
              withVerticalLines={true}
              fromZero={false}
              segments={15} //Horizontal-Lines
              
              verticalLabelRotation={0} //Not Working in browser
              xLabelsOffset={0}

              chartConfig={{
                backgroundColor: Colors.white,
                backgroundGradientFrom: Colors.white,  //#FFFFFF
                backgroundGradientTo: Colors.white,     
                decimalPlaces: 1, // optional, defaults to 2dp
                color: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,  //Lines / Grid
                labelColor: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`, //Lables
                style: {
                  borderRadius: 16,
                  height: height,
                },
                propsForDots: {
                  r: "5",
                  strokeWidth: "0.5",
                  stroke: Colors.white
                }
              }}
            />):(<></>)}
          </ScrollView>
        </View>
        {!this.props.Display?(
          <View style={[Helpers.fullWidth, Helpers.center]}>
            <View style={[Helpers.borderContainer, Helpers.fullWidth, Helpers.rowCenter]}>
              <View style={{paddingRight: 2.5}}>
                <Icon name="md-arrow-back" type='ionicon' size={25} color="black" onPress={() => {
                  if (this.state.ActiveIndex != 0){
                    var i = this.state.ActiveIndex;
                    i = i-1;
                    this.setState({
                      ActiveIndex: i
                    })
                  }
                }}/>
              </View>
              <Button Text="Day view" buttonStyle={{width:wide?100:80}}
                onPress={() => {
                  this.setState({
                    ActiveIndex: -1,
                    ActiveLength: "Days"
                  })
                } }/>
              <Button Text="Week view" buttonStyle={{width:wide?100:80, marginLeft:2.5}}
                onPress={() => {
                  this.setState({
                    ActiveIndex: -1,
                    ActiveLength: "Weeks"
                  })
                } }/>
              <Button Text="Full Batch" buttonStyle={{width:wide?100:80, marginLeft:2.5}}
                onPress={() => {
                  this.setState({
                    ActiveIndex: 0,
                    ActiveLength: "Batch"
                  })
                } }/>
              <View style={{paddingLeft: 2.5}}>
                <Icon name="md-arrow-forward" type='ionicon' size={25} color="black" onPress={() => {
                  var i = this.state.ActiveIndex;
                  i = i+1;
                  this.setState({
                    ActiveIndex: i
                  })
                }}/>
              </View>
            </View>
            <View style={[Helpers.fullWidth, Helpers.rowCenter]}>
              {BatchIsOpen?(
                <CurveEditor control={this} props={this.props} state={this.state} />
              ):(<></>)}
            </View>
          </View>
        ):(<></>)}
      </View>
    )
  }
}

const CurveEditor = (props) => {
  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    if (props.state.ViewPoint){
      var TimeText = props.state.Labels[props.state.ViewPoint.index]
      if (props.state.ViewPoint.dataset.dataX) {
        var Time = new Date(props.state.ViewPoint.dataset.dataX[props.state.ViewPoint.index])
        TimeText = Time.getFullYear() + "." + (Time.getMonth() +1) + "." + Time.getDate()
      }

      var Data = props.state.ViewPoint.value
      return (
        <View style={[Helpers.buttonContainer, Helpers.fullWidth, Helpers.rowCenter, {maxHeight:100}] }>
          <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
            <Text>{TimeText}</Text>
          </View>
          <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
            <Text>{Data} Kg.</Text>
          </View>
        </View>
      )
    }
    else return(<></>)
  } else {
    const [Point, setPoint] = React.useState(0)
    if (!props.state.TempGraph) {
      return(<></>)
    }
    else {
      return (
        <View style={[Helpers.buttonContainer, Helpers.fullWidth, Helpers.rowCenter, {maxHeight:100,marginTop:10}] }>
          <View style={{padding:6, paddingRight: 15}}>
            <Icon name="md-arrow-back" type='ionicon' size={25} color="black" onPress={() => {
              var index = Point;
              index = index -1
              if (index < 0) {
                index = props.state.TempGraph.data.length -1
              }
              setPoint(index)
            }}/>
          </View>
          <Input
              type='number'
              step="0.1"

              label={props.state.Labels[Point]}
              containerStyle={[{width:"auto", borderWidth:1}]}
              style={[ApplicationStyles.input, Helpers.borderContainer, {borderRadius:0}]}

              onChange= {(evt) => {
                var text = evt.target.value
                if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                  var NewData = props.state.TempGraph.data;
                  NewData[Point] = text
                  
                  props.control.setState({
                    state: props.state,
                    TempGraph: {...props.state.TempGraph, data: NewData}
                  })
                  props.control.setGraphPeriode(props.state.ActiveLength)
                }
              }}
              /*onChangeText={text => {
                if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                  var NewData = props.state.TempGraph.data;
                  if (text === "" || text === null) text = 0
                  if (text.endsWith('.')) text = text + "0"
                  NewData[Point] = parseFloat(text)
                  console.log(NewData[Point], text)
                  props.control.setState({
                    TempGraph: {...props.state.TempGraph, data: NewData}
                  })

                  props.control.forceUpdate()
                }
              }}*/
              value={
                props.state.TempGraph.data[Point]
              }
            />
          <View style={{padding:6, paddingRight: 15}}>
            <Icon name="md-arrow-forward" type='ionicon' size={25} color="black" onPress={() => {
              var index = Point;
              index = index +1
              if (index > props.state.TempGraph.data.length -1){
                index = 0
              }
              setPoint(index)
            }}/>
          </View>
          <Input
              label="Curve Name"
              containerStyle={[{width:"auto", borderWidth:1}]}
              style={[ApplicationStyles.input, Helpers.borderContainer, {borderRadius:0}]}
              
              onChangeText={text => {
                /*var name = text;
                name = name.replace(' ', '_')
                if (isNaN(name[0])){
                  var NewLegends = props.state.Legends;
                  NewLegends[0] = name
                  props.control.setState({
                    TempGraph: {...props.state.TempGraph, name: name},
                    Legends: NewLegends
                  })
                }*/
              }}
              value={
                props.state.TempGraph.name
              }
            />
        </View>
      )
    }
  }
} 

