import React, { Component } from 'react'
import { StyleSheet, View, TouchableOpacity, Picker, Text, Image} from 'react-native';

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors, Images } from '../Theme'

const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];

class DateTime_Picker extends Component {
    constructor(props){
        super(props);
        
        const {year, month, day} = this.getStates(props)
        this.state = {
            year: year,
            month: month,
            day: day,
        }
    }

    getStates(props){
        var Today = new Date();
        var year, month, day;
        if (props.FullYear) {
            Today = new Date(props.FullYear)
        }

        year = Today.getFullYear()
        month = Today.getMonth()
        day = Today.getDate()

        if (!props.FullYear) {
            if (props.Year){
                year = this.props.Year
            }
            if (props.Month){
                month = this.props.Month
            }
            if (props.Day){
                day = this.props.Day
            }
        }
        return {year, month, day}
    }

    componentDidUpdate(prevProps, prevState){
        if (prevProps.FullYear){
            if (prevProps.FullYear != this.props.FullYear){
                this.forceUpdate()
            }
        }else {
            var changed = false;
            if (prevProps.Year != this.props.Year) changed = true
            if (prevProps.Month != this.props.Month) changed = true
            if (prevProps.Day != this.props.Day) changed = true
            if (changed) {
                this.forceUpdate()
            }
        }
    }

    render() {
        var {year, month, day} = this.getStates(this.props)

        var YearItems = []
        for (let index = year - 25; index < year + 25; index++) {
            YearItems.push(<Picker.Item key={"Year_" + index} value={index} label={index} />)
        }
        var MonthItems = []
        for (let index = 0; index < monthNames.length; index++) {
            MonthItems.push(<Picker.Item key={"Month_" + index} value={index} label={monthNames[index]} />)
        }

        var DayItems = []
        var d = new Date(year, month + 1, 0).getDate()
        for (let index = 1; index <= d; index++) {
            DayItems.push(<Picker.Item key={"Day_" + index} value={index} label={index} />)
        }

        return(
            <View style={[Helpers.fullWidth, Helpers.row, this.props.style]}>
                <Picker selectedValue={year} style={[{minWidth: 100, marginRight:10}]}
                    onValueChange={(_year) => {
                        year = _year
                        this.props.onValueChange(new Date(year, month, day))
                    }}>
                    {YearItems}
                </Picker>

                <Picker selectedValue={month} style={[{minWidth: 100, marginRight:10}]}
                    onValueChange={(_month) => {
                        month = _month
                        this.props.onValueChange(new Date(year, month, day))
                    }}>
                    {MonthItems}
                </Picker>

                <Picker selectedValue={day} style={[{minWidth: 100}]}
                    onValueChange={(_day) => {
                        day = parseInt(_day)
                        this.props.onValueChange(new Date(year, month, day))
                    }}>
                    {DayItems}
                </Picker>
            </View>
        )
    }
}
export default DateTime_Picker;