//#region Firebase Auth services
function createFirebaseUserWithemailandpassword({Email, Pass}) {
  return new Promise((resolve, reject) => {
    firebase.auth().createUserWithEmailAndPassword(Email,Pass).then(() => {

      const User = firebase.auth().currentUser;
      var Name = User.displayName ? User.displayName : User.email
      actionStarting(DataRefs.USERSDATA, Actions.UPDATE, {UserId: User.uid, Data: {
        name: Name,
        state: UserStates.STARTER,
      }}).then((data) => {
        resolve(data)
      })

    }).catch(error => {
      console.log("UserCreate Error: ", error);

      if (error.code === 'auth/email-already-in-use') {
        resolve({error:'The email address is already in use.'})
      }
      if (error.code === 'aith/invalid-email') {
        resolve({error:'The email addres is invalid.'})
      }
    })
  })
}

function signinWithemailandpassword({Email, Pass}){
  if (firebase.auth().currentUser){
    if (firebase.auth().currentUser.email != Email){
      const res = logoutUser()
    }
    else {
      return new Promise((resolve, reject) => {
        resolve(true)
      })
    }
  }
  return new Promise((resolve, reject) => {
    firebase.auth().signInWithEmailAndPassword(Email,Pass).then(() => {
      resolve(true)
    }). catch(error => {
      console.log("User Signin Error: ", error);

      if (error.code === 'auth/email-already-in-use') {
        resolve('The email address is already in use.')
      }
      if (error.code === 'aith/invalid-email') {
        resolve('The email addres is invalid.')
      }
      else {
        resolve('Could not singin')
      }
    })
  })
}

function logoutUser(){
  return new Promise((resolve, reject) => {
    firebase.auth().signOut().then(() => {
      resolve(true)
    })
  })
}
