//#region Master Control
function actionStarting(ref, action, params = null){
  const CurrentUser = firebase.auth().currentUser;
  switch(ref){
    case DataRefs.USERSDATA:
      switch(action){
        case Actions.FETCH:
          return fetchUser(params)
        case Actions.CREATE:
          if (params.hasOwnProperty("UserData")){
            return new Promise((resolve, reject) => {
              var Data = params[0]
              createFarm({Data:{name: params.Farms.name}}).then((farmid) => {
                updateFarmData({FarmId:farmid, Data:{sections:params.Sections, pens: params.Pens}}).then(() => {
                  updateUserData({Data:{state: params.UserData.state, farms: {[farmid]: true}}}).then(() => {
                    fetchUser(CurrentUser.uid).then((data) => {
                      resolve({userData: data})
                    });
                  })
                })
              })
            })
          }else 
          return createFirebaseUserWithemailandpassword(params)
        case Actions.UPDATE:
          return updateUserData(params)
        case Actions.SIGN:
          if (!params){
            return logoutUser()
          }else {
            return signinWithemailandpassword(params)
          }
        case Actions.CLEAR:
          if (!CurrentUser){
            return {type:Actions.CLEAR, path:DataRefs.USERS, data:{}}
          } else {
            return {type:Actions.CLEAR, path:DataRefs.USERSDATA, data:{}}
          }
      }
      break;

    case DataRefs.USERS:
      switch(action){
        case Actions.FETCH:
          return fetchUser(params, true)
        case Actions.CREATE:
          break;
        case Actions.UPDATE:
          var FarmId = params.FarmId
          return {type:Actions.UPDATE, path:DataRefs.USERSDATA, data:{farms:{[FarmId]: true}}}
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.USERS, data:{}}
      }
      break;

    case DataRefs.FARMS:
      switch(action){
        case Actions.FETCH:
          return fetchFarm(params);
        case Actions.CREATE:
          break;
        case Actions.UPDATE:
          console.log("??")
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.FARMS, data:{}}
      }
      break;

    case DataRefs.FARMDATAS:
      console.log("FarmsDatas ???") //TODO: propperly no use.. (clean up)
      break;

    case DataRefs.SECTIONS:
      switch(action){
        case Actions.FETCH:
          return fetchSections(params);
        case Actions.CREATE:
          break;
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.SECTIONS, data:{}}
      }
      break;

    case DataRefs.PENS:
      switch(action){
        case Actions.FETCH:
          return fetchPens(params);
        case Actions.CREATE:
          return createNewPen(params);
        case Actions.DELETE:
          return deletePen(params)
        case Actions.UPDATE:
          return updatePen(params);
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.PENS, data:{}}
      }
      break;

    case DataRefs.PIGS:
      switch(action){
        case Actions.FETCH:
          return fetchPigs(params);
        case Actions.CREATE:
          return updatePigdata(params);
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.PIGS, data:{}}
      }
      break;

    case DataRefs.BATCHS:
      switch(action){
        case Actions.FETCH:
          return fetchBatchs(params)
        case Actions.CREATE:
          return createBatchs(params)
        case Actions.UPDATE:
          return updateBatchs(params)
        case Actions.DELETE:
          //return deleteBatch(params)
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.BATCHS, data:{}}
      }
      break;

    case DataRefs.WEIGHING:
      switch(action){
        case Actions.FETCH:
          return fetchWeights(params);
        case Actions.CREATE:
          return createWeight(params);
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.WEIGHING, data:{}}
      }
      break;

    case DataRefs.COMMENTS:
      switch(action) {
        case Actions.FETCH:
          return fetchComments(params);
        case Actions.CREATE:
          return createComment(params);
      }
      break;

    case DataRefs.GRAPH:
      switch(action){
        case Actions.FETCH:
          return fetchGraphData(params);
        case Actions.CREATE:
          return updateGraphData(params);
        case Actions.UPDATE:
          return updateGraphData(params);
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.GRAPH, data:{}}
      }
      break;
  }
  return new Promise((resolve, reject) => {
    resolve({error: "Unknown Action"})
  })
}
