function SetUpdateTime(DataRef) {
  return new Promise((resolve, reject) => {
    console.log("Setting Update Time")
    firebase.database().ref(DataRef).update({
      update_time: firebase.database.ServerValue.TIMESTAMP,
    }).then(() => {
      resolve(false)
    }).catch(error => {
      console.log(error)
      /*if (error.code === 'database/permission-denied') {
        console.log("Batch update was permission denied. ");
      }else if (error.code) {
        console.log("Batch update Error: ", error);
      }*/
      resolve(true)
    });
  })
}
function CheckDataTime(DataRef, DataTime) {
  return new Promise((resolve, reject) => {
    firebase.database().ref(DataRef + "/update_time").once('value').then((_Time) => {
      var UpdateTime = _Time.val()
      if (!UpdateTime) {
        SetUpdateTime(DataRef).then((res) => {
          resolve(true)
        })
      } else {
        if (DataTime) {
          if (DataTime === "SET"){
            SetUpdateTime(DataRef).then((res) => {
              resolve(res)
            })
          } else if (DataTime < UpdateTime){
            resolve(true)
          }
          else resolve(false)
        }else resolve(true)
      }
    })
  })
}

//#region User data Controls
function fetchUser({UserId}, List = false) {
  var user = "";
  if (UserId) user = UserId
  else if (!List && firebase.auth().currentUser) user = firebase.auth().currentUser.uid;
  if (user || List) {
    const ref = "users/" + user;
    return firebase.database().ref(ref).once('value').then((userData) => {
      var data = userData.val()
      if (!List) {
        return {type:Actions.UPDATE, path:DataRefs.USERSDATA, data:{data}}
      } else {
        return {type:Actions.UPDATE, path:DataRefs.USERS, data:{data}}
      }
    })
  }
  else return new Promise((resolve, reject) => {
    console.log("User uid in fecth unknown");
    resolve(null)
  })
}

function updateUserData({UserId, Data}) {
  const user = UserId? UserId: firebase.auth().currentUser.uid;
  if (user) {
    const ref = "users/" + user;
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).update(
        Data
      ).then((data) => {
        resolve(null)
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("User update was permission denied. ");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }
        resolve({error:"An error occurred"})
      })
    }) 
  } else return new Promise((resolve, reject) => {
    console.log("User uid in fecth unknown");
    resolve(null)
  })
}
//#endregion

//#region Farm info data Controls
function fetchFarm({FarmId}) {
  var ref = "farms/" + FarmId
  return firebase.database().ref(ref).once('value').then((_farmData) => {
    //TODO:Multiple farms
    var data = _farmData.val()
    if (FarmId) {
      data = {[FarmId]:data}
    }
    return {type:Actions.UPDATE, path:DataRefs.FARMS, data: data}
  })
  /*} else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })*/
}

function updateFarm({FarmId, Data}) {
  return new Promise((resolve, reject) => {
    if (FarmId){
      firebase.database().ref('farms/').update({
        Data
      }).then(()=> {
        resolve(null)
      })
    } else {
      const farm = firebase.database().ref('farms/').push()
      const farmid = farm.key
      farm.set({
        Data
      }).then(() => {
        resolve(farmid)
      }).catch(error => {
        console.log("Create Farm Error: ", error);
        resolve(false)
      })
    }
  })
}

function createFarm({Data}){
  const user = firebase.auth().currentUser;
  const ref = "users/" + user.uid;

  return new Promise((resolve, reject) => {
    const farm = firebase.database().ref('farms/').push()
    const farmid = farm.key
    
    farm.set({
      email: user.email,
      manager: user.displayName ? user.displayName : user.email,
      name: Data.name
    }).then(() => {
      resolve(farmid)
    }).catch(error => {
      console.log("Create Farm Error: ", error);
      resolve(false)
    })
  })
}

function updateFarmData({FarmId, Data}) {
  if (FarmId) {
    const ref = "farms_data/" + FarmId;
    
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref).update(
        Data
      ).then(() => {
        resolve(null)
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("Farm data update was permission denied. ");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }

        resolve(false)
      })
    })
  } else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })
}
