//#region Sections
function fetchSections({FarmId, SectionId, DataTime}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/sections";
    if (SectionId){
      ref += "/" + SectionId
    }
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/sections", DataTime).then((DoFetch) => {
        console.log("Doing Sections fetch", DoFetch)
        if (DoFetch){
          return firebase.database().ref(ref).once('value').then((_sectionidData) => {
            resolve({type:Actions.UPDATE, path:DataRefs.SECTIONS, data:_sectionidData.val()})
          })
        }
        else {
          resolve({type:Actions.UPDATE, path:DataRefs.SECTIONS, data:{}})
        }
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}
