//#region Pens
function fetchPens({FarmId, PenId, DataTime}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/pens", DataTime).then((DoFetch) => {
        console.log("Doing Pens fetch", DoFetch)
        if (DoFetch){

          firebase.database().ref(ref).once('value').then((_pensData) => {
            if (PenId) {
              var penData = _pensData.val()
              if (!penData) penData = {}
              if (!penData.datas) penData.datas = {}
              //if (!penData.datas.gcurve) penData.datas.gcurve = "default";
      
              var pen = {[PenId]: penData};
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:pen})
            } else {
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_pensData.val()})
            }
          })
        }
        else {
          resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:{}})
        }
      })
    })
  } 
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function createNewPen({FarmId, PenId, SectionId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        var ref2 = ref + "pens/" + PenId + "/"
        firebase.database().ref(ref2).set({
          section: SectionId,
        }).then(() => {
          firebase.database().ref(ref + "sections/" + SectionId + "/pens").update({
            [PenId]: true,
          }).then(() => {
            firebase.database().ref(ref + "sections/").update({
              update_time: firebase.database.ServerValue.TIMESTAMP,
            }).then(() => {
              fetchPens({FarmId: FarmId, PenId: PenId, DataTime: "SET"}).then((_Pen) => {
                resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
              });
            });
          });
        });
      }
    })
  }
}

function deletePen({FarmId, PenId, SectionId}){
  if (FarmId && PenId){
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      firebase.database().ref(ref + "pens/" + PenId).remove().then(() => {
        if (SectionId){
          firebase.database().ref(ref + "sections/" + SectionId + "/pens/" + PenId).remove().then(() => {
            firebase.database().ref(ref + "sections/").update({
              update_time: firebase.database.ServerValue.TIMESTAMP,
            }).then(() => {
              fetchPens({FarmId: FarmId, PenId: PenId, DataTime: "SET"}).then((_Pen) => {
                resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
              })
            });
          })
        }else {
          resolve(null)
        }
      })
    })
  }
}

function updatePen({FarmId, PenId, Pen}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        fetchPens({FarmId: FarmId, PenId: PenId}).then((_Pen) => {
          var ref2 = ref + "pens/" + PenId + "/"
          var data = Pen.datas
          var Datas = {..._Pen.data[PenId].datas, ...data}
          firebase.database().ref(ref2).update({
            ...Pen,
            datas: Datas
          }).then(() => {
            if (Pen.section) {
              firebase.database().ref(ref + "sections/" + SectionId + "/pens").update({
                [PenId]: true,
              }).then(() => {
                firebase.database().ref(ref + "sections/").update({
                  update_time: firebase.database.ServerValue.TIMESTAMP,
                }).then(() => {
                  resolve(null)
                });
              });
            }
          });
        })
      }
    })
  }
}
