//#region Weights
function fetchWeights({FarmId, PenId, SectionId, limitStart = 0, limitTo = 5, DataTime}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/weighings", DataTime).then((DoFetch) => {
        console.log("Doing Weight fetch", DoFetch)
        //if (DoFetch){
          //TODO:Weight fetch is still done due mem save state...
          if (PenId) {
            ref += "/" + PenId
            firebase.database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
              var weighing = {[snapshot.key]:{}}
              snapshot.forEach(c => {
                weighing[snapshot.key][c.key] = c.val()
              }) 
              resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:weighing})
            })
          } else {
            //TODO: Section fetch cast :: in mem check ??
            fetchSections({FarmId, SectionId}).then((sections) => {
              var itemsProcessed = 0;
              var length = 0;
              var newWeights = {}
              Object.keys(sections.data).forEach(section => {
                if (sections.data[section] && sections.data[section].pens) {
                  length += Object.keys(sections.data[section].pens).length
                  Object.keys(sections.data[section].pens).filter(e => e !== '__jsogObjectId').forEach(pen => {
                    fetchWeights({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}).then((weights) => {
                      itemsProcessed++
                      newWeights = {...newWeights, ...weights.data}
                      if (itemsProcessed === length) {
                        resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                      }
                    }).catch(error => {
                      console.log(error)
                      resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                    })
                  })
                }
              })
            }).catch(error => {
              console.log(error)
            })
          }

        /*}
        else {
          resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:{}})
        }*/
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Weights Error");
    resolve(null)
  })
}

function createWeight({FarmId, PenId, Weight, Date}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      ref += "/" + PenId + "/"

      return new Promise((resolve, reject) => {
        fetchPens({FarmId: FarmId, PenId: PenId}).then((pen) => {
          const weightRef = firebase.database().ref(ref).push()
          const weightId = weightRef.key
          var count = 1
          if (pen && pen.data) {
            var Pen = pen.data[PenId]
            console.log(Pen, PenId)
            if (Pen && Pen.datas && Pen.datas.count) {
              count = Pen.datas.count
            }
          }
          weightRef.set({
            time: Date,
            weight: Weight,
            count: count,
          }).then(() => {
            fetchWeights({FarmId:FarmId, PenId:PenId}).then((weights)=>{
              resolve(weights)
            })
          }).catch(error => {
            console.log("Create Weight Error: ", error);
            resolve(null)
          })
        })
      })
    }
  }
}
