import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, SafeAreaView, FlatList } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, OperationsPanel, CustomListItem, ContentProp } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { FlatGrid, SectionGrid } from 'react-native-super-grid';

class SystemListBoard extends Component {
  constructor(props){
    super(props);

    const {FarmList} = this.setData(props)
    this.state = {
      FarmList: FarmList,
      Farm: {name: ""}
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore

    this.props.getFarms()
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore

    var FarmList = null
    if (localDataStore && localDataStore.farms) {
      FarmList = sortObject(localDataStore.farms)
    }
    return {FarmList}
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null){
      NavigationService.navigate("AuthStack")
    }
    //Mabye call check for localdataStore get farms..
  }

  componentDidMount() {
    this.checkData(this.props);

    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase) {
        if (this.props.firebase.localDataStore) {
          if (this.props.firebase.localDataStore.farms && JSON.stringify(this.state.FarmList)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.farms))) {
            const {FarmList} = this.setData(this.props)
            this.setState({
              FarmList: FarmList,
            })
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid
    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser?(
            <View style={[Helpers.fill, Helpers.buttonContainer, Helpers.fullWidth, Metrics.bottomMargin, Helpers.column]}>
              {this.state.FarmList ? (
                <View style={[Helpers.fill, Helpers.borderContainer, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                  <SafeAreaView style={styles.container}>
                    <FlatList
                      data={Object.keys(this.state.FarmList).filter(e => e !== '__jsogObjectId')}
                      renderItem={({item, index})=>{
                        var Farm = this.state.FarmList[item];
                        Farm.id = item
                        return(
                          <View style={[Helpers.fill, Helpers.rowCenter, Helpers.fullWidth, {height:35}]}>
                            <CustomListItem Title={Farm.name} itemStyle={[{minWidth: 100, height:35,}]} onPress={() => {
                                //this.props.setData(item, Section, Pen)
                                //NavigationService.navigate("DataDisplay")
                              }}/>

                            <CustomListItem itemStyle={[{minWidth:150, height:35, justifyContent:"center"}]} Title={Farm.manager} onPress={() => {
                                this.setState({
                                  Farm: Farm
                                })
                              }}/>

                            <CustomListItem itemStyle={[{minWidth:150, height:35}]} Title={Farm.email} onPress={() => {
                                this.setState({
                                  Farm: Farm
                                })
                              }}/>
                          </View>
                        )
                      }}
                      keyExtractor={(item, index) => index.toString()}
                    />
                  </SafeAreaView>
                </View>
              ):(<></>)} 
              <View style={[Helpers.rowCenter, Helpers.fullWidth, {height:120}]}>
                <Input
                  label={"Farm-tag:"}
                  containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                  style={[ApplicationStyles.input, ]}
                  editable={false}
                  onChangeText={text => {
                    var Farm = this.state.Farm;
                    Farm.name = text;
                    this.setState({
                      Farm: Farm
                    })
                  }}
                  value={
                    this.state.Farm.name
                  }
                />
                <Button Text="Consys"
                  boxStyle={[{marginLeft:10}]}
                  onPress={() => {
                    //this.props.logOut()
                  }}/>
                <Button Text="Farm Panel"
                  boxStyle={[{marginLeft:10}]}
                  onPress={() => {
                    if (this.state.Farm.id !== "") {
                      this.props.setUserFarm(this.state.Farm.id)
                      NavigationService.navigate("UserPanel")
                    }
                  }}/>
              </View>
            </View>
          ):(<></>)}
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const styles = StyleSheet.create({
  circle: {
    width: 125,
    height: 125,
    borderRadius: 125/2,
    marginTop: Metrics.tiny,
    marginLeft: Metrics.tiny,
  },
});

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    getFarms: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {FarmId:""})),

    setUserFarm: (FarmId) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.USERS, {FarmId: FarmId})),
    
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(SystemListBoard);